$output-bourbon-deprecation-warnings: false;
@import "bourbon";
@import "vars";
@import "functions";

.slick-list, .slick-track {
	min-width: 100%; }

*::-webkit-input-placeholder {
	color: #afafaf;
	opacity: 1; }

*:-moz-placeholder {
	color: #afafaf;
	opacity: 1; }

*::-moz-placeholder {
	color: #afafaf;
	opacity: 1; }

*:-ms-input-placeholder {
	color: #afafaf;
	opacity: 1; }
.forms {
	input, textarea {
		-webkit-appearance: none;

		&:ms-expand {
			display: none; } } }
html {
	font-size: 100%; }

input, a, button, textarea, select {
	outline: none; }

*, *:after, *:before {
	box-sizing: border-box; }

[v-cloak] {
	display: none; }

%clear-text {
	overflow: hidden;
	text-indent: -9999px; }

%clearfix {

	&:after {
		display: table;
		clear: both;
		content: ""; } }

%clear-select, select {
	-webkit-appearance: none;
	/* for FF */
	-moz-appearance: none;
	text-indent: 0.01px;
	text-overflow: '';
	/* for IE */
	-ms-appearance: none;
	appearance: none!important;

	&::-ms-expand {
		display: none; } }

address {
	font-weight: 300; }

body {
	min-width: 320px;
	line-height: 1.2;
	font-family: $default-font;
	font-weight: 300;
	font-size: rem(14);
	// background: $second-color
	color: #000;
	overflow-x: hidden;
	letter-spacing: 0.01rem;

	&.inner {

		#content {
 } } }			// padding-bottom: 100px

a {
	color: $main-color;
	text-decoration: underline;
	font-weight: 300;

	&[href^="tel"], &[href^="mail"] {
		white-space: nowrap; } }

#content {
	background: #fff;
	margin: 0 auto;
	width: 100%;
	z-index: 40;
 }	// min-height: 100%

.wrapper {
	max-width: $base-width + px;
	padding: 0 2rem;
	margin: 0 auto;
	box-sizing: content-box; }

input[type="submit"], button {
	cursor: pointer; }

img {
	max-width: 100%; }

li {
	list-style: none; }

#page-wr {
	position: relative;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	z-index: 40;
	// background: $second-color
	transition: .5s; }

.footer {
	margin-top: auto;
	z-index: 40; }

.soc {
	display: flex;

	&__link {
		display: flex;
		@include size(40px);
		background: {
			position: center;
			repeat: no-repeat; }

		&:not(:last-child) {
			margin-right: 11px; } } }

.msgs {
	display: flex;
	align-items: center;

	.soc__link {
		background-color: transparent; } }

.btn {
	position: relative;
	color: #fff;
	text-decoration: none;
	display: flex;
	align-items: center;
	border: none;
	cursor: pointer;

	&--rectangle {
		border-radius: 0; } }

.image {
	margin-bottom: 50px;
	&--right {
		float: right;
		margin-left: 40px; }

	&--left {
		float: left;
		margin-right: 40px; }
	&--figcaption {
		padding: 30px 20px;
		background: $second-color;
		text-align: center;
		margin-top: 30px; } }

%text-page, .text-page {



	> *:not(:first-child) {
		margin-top: 22px; }

	h1 {
		font-size: rem(40);
		font-weight: 300; }
	h2, h3, h4, h5, h6, strong {
 }		//font-weight: bold

	h2 {
		font-size: rem(22);
		line-height: rem(28); }

	h3 {
		font-size: rem(20);
		line-height: rem(26); }

	h4, h5 {
		line-height: rem(24); }

	h4 {
		font-size: rem(18); }

	h5 {
		font-size: rem(16); }

	img[align="right"] {
		float: right;
		margin-left: 20px; }

	img[align="left"] {
		float: left;
		margin-right: 20px; }

	blockquote {
		border-left: 2px solid $main-color;
		padding-left: 21px; }

	table {
		width: 100%;
		table-layout: fixed;
		color: #000;

		td, th {
			padding: 15px 23px;
			font-weight: 300;
			vertical-align: middle; } }

	table[border="1"] {

		td, th {
			border: 1px solid $border-color; } }

	ul, ol {
		max-width: 710px;
		line-height: rem(24); }

	li {
		//padding-left: 20px
		position: relative;

		&:before {
			display: inline-block; }

		&:not(:first-child) {
 }			//margin-top: 21px

		ul, ol {
			margin-bottom: 20px; }

		li {
			padding-left: 20px;

			&:not(:first-child) {
				margin-top: 0; } } }

	ul {
		margin-bottom: 30px;

		li {

			&:before {
				content: "";
				background: #000;
				width: 12px;
				height: 1px;
				vertical-align: middle;
				margin: 0 14px 4px 0; } }

		ul {

			li {
				//padding-left: 20px

				&:before {
					content: "-";
					@include size(auto);
					background: none;
					margin: 0 5px 5px 0; } } } }

	ol {
		margin-left: 15px;
		line-height: rem(24);

		li {
			list-style: inherit;
			padding-left: 10px; }

		// counter-reset: i

		// li
		// 	counter-increment: i

		// 	&:before
		// 		content: counter(i) "."
 }		// 		margin-right: 4px
	dt, dd {
		display: inline;
		font-style: italic; }

	blockquote {
		position: relative;
		display: inline;
		&:before {
			content: "\201c";
			left: -20px;
			top: -15px;
			position: absolute;
			font-size: 40px;
			color: #323c8d; }
		&:after {
			content: "\201c";
			right: -20px;
			bottom: -40px;
			position: absolute;
			font-size: 40px;
			color: #323c8d; } }

	p {
		line-height: rem(24); }

	.table-wrap {
		overflow-x: scroll;
		table {
			width: auto;
			table-layout: fixed; } } }

