		transition: top .3s; }

	.burger {
		position: relative;
		z-index: 100;
		display: flex;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding: 15px;
		background: 0 0;
		border: 0;
		user-select: none;
		margin-left: auto;
		cursor: pointer;
		// background: rgba(#000, .4)
		border-radius: 3px;
		&:before {
			display: none; }
		span {
			position: relative;
			display: inline-block;
			width: 30px;
			height: 3px;
			vertical-align: bottom;
			background: $main-color;
			transition: background .2s .4s;
			&:before {
				border-radius: 0;
				transform: none;
				content: "";
				position: absolute;
				left: 0;
				top: 10px;
				width: 100%;
				height: 3px;
				background: $main-color;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; }
			&:after {
				content: "";
				position: absolute;
				left: 0;
				top: -10px;
				width: 100%;
				height: 3px;
				background: $main-color;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; } } }

	.mobile-menu--open {
		.mobile-menu {
			position: absolute;
			top: 0;
			right: 0;
			left: 0; }
		footer {
			display: none; }
		.content {
			pointer-events: none;
			position: fixed;
			&:before {
				content: "";
				position: fixed;
				width: 100%;
				height: 100%;
				background: rgba(#000, .4);
				z-index: 10;
				pointer-events: none; } }

		.head {
			transform: translate3d(0, 0, 0);
			z-index: 20;
			opacity: 1;
			pointer-events: all; }


		.burger {
			> span {
				background: 0 0; } }
		.burger {
			> span {
				&:before {
					transform: rotate3d(0,0,1,45deg);
					top: 0;
					transition: top .3s,transform .3s .5s; } } }
		.burger {
			> span {
				&:after {
					transform: rotate3d(0,0,1,-45deg);
					top: 0;
					transition: top .3s,transform .3s .5s; } } } }
	.content {
		margin: 0;
		padding: 0; }

	.head {
		transform: translate3d(0, 0, 0);
		transition: transform .3s;
		position: fixed;
		top: 0;
		bottom: 0;
		display: flex;
		flex: 1 1 auto;
		width: 100%;
		justify-content: center;
		left: initial;
		opacity: 0;
