	.loader .el-1 {
		animation: el-one 6s ease forwards 1s;
		stroke-dasharray: 11000;
		stroke-dashoffset: 11000; }



	@Keyframes el-one {
		0% {}


		10% {
			stroke-dashoffset: 3500;
			stroke-dashoffset: 0; }
		80% {

			stroke-dashoffset: 3500;
			stroke-dashoffset: 0; }
		81% {
			fill-opacity: 1; }
		82% {

			stroke-dashoffset: 3500;
			stroke-dashoffset: 0; }
		83% {}
		100% {
			fill-opacity: 1; } }

	.loader .el-2 {
		animation: el-two 6s ease forwards 1s;
		stroke-dasharray: 9500;
		stroke-dashoffset: 9500; }

	@Keyframes el-two {
		0% {}


		10% {
			stroke-dashoffset: 5000;
			stroke-dashoffset: 0; }
		80% {

			stroke-dashoffset: 5000;
			stroke-dashoffset: 0; }
		81% {
			fill-opacity: 1; }
		82% {

			stroke-dashoffset: 5000;
			stroke-dashoffset: 0; }
		83% {}
		100% {
			fill-opacity: 1; } }

	.loader .el-3 {
		animation: el-three 6s ease forwards 1s;
		stroke-dasharray: 24000;
		stroke-dashoffset: 24000; }

	@Keyframes el-three {
		0% {}


		10% {
			stroke-dashoffset: 8000;
			stroke-dashoffset: 0; }
		80% {

			stroke-dashoffset: 8000;
			stroke-dashoffset: 0; }
		81% {
			fill-opacity: 1; }
		82% {

			stroke-dashoffset: 8000;
			stroke-dashoffset: 0; }
		83% {}
		100% {
			fill-opacity: 1; } }

	.loader .el-4 {
		animation: el-four 6s ease forwards 1s;
		stroke-dasharray: 56000;
		stroke-dashoffset: 56000; }

	@Keyframes el-four {
		0% {}


		10% {
			stroke-dashoffset: 17000;
			stroke-dashoffset: 0; }

		80% {
			fill-opacity: 1; }
		81% {

			stroke-dashoffset: 17000;
			stroke-dashoffset: 0; }
		83% {}

