	0% {}


	10% {
		stroke-dashoffset: 5000;
		stroke-dashoffset: 0; }
	80% {

		stroke-dashoffset: 5000;
		stroke-dashoffset: 0; }
	81% {
		fill-opacity: 1; }
	82% {

		stroke-dashoffset: 5000;
		stroke-dashoffset: 0; }
	83% {}
	100% {} }

.loader .el-3 {
	animation: el-three 6s ease forwards 1s;
	stroke-dasharray: 24000;
