 }	// fill-opacity: 0

@Keyframes el-three {
	0% {}


	10% {
		stroke-dashoffset: 8000;
		stroke-dashoffset: 0; }
	80% {

		stroke-dashoffset: 8000;
		stroke-dashoffset: 0; }
	81% {
		fill-opacity: 1; }
	82% {

		stroke-dashoffset: 8000;
		stroke-dashoffset: 0; }
	83% {}
	100% {} }
.loader .el-4 {
	animation: el-four 6s ease forwards 1s;
	stroke-dasharray: 56000;
	stroke-dashoffset: 56000;
 }	// fill-opacity: 0

@Keyframes el-four {
	0% {}


	10% {
		stroke-dashoffset: 17000;
